import {
  CDatePicker,
  CInput,
  CSelect,
  CSelectOptionActiva,
  CSelectOptionCurrency,
  CSelectOptionFlag,
  CTextArea,
} from "@components";
import { Col, Row, Form } from "antd";

const CEditAssetInput = ({
  forms,
  handleChange,
  error,
  onBranch,
  listBranch,
  onLocation,
  listLocation,
  onDepartement,
  departement,
  onStatus,
  onRemark,
  listStatus,
  listAssetType,
  onAssetType,
  onCodeActiva,
  listCodeActiva,
  onOwnersip,
  listOwnership,
  onCurrency,
  onActiva,
  onFlag,
  listAnggaran,
  onAnggaran,
  onChangeDatePerolehan,
}) => {
  const formItemLayout = {
    labelCol: {
      xs: { span: 24 },
      sm: { span: 8 },
    },
    wrapperCol: {
      xs: { span: 24 },
      sm: { span: 12 },
    },
  };
  return (
    <Row gutter={4} style={{ flex: 1, marginTop: 45 }}>
      <Col span={12}>
        <Form {...formItemLayout}>
          <CInput
            label="No Induk Barang "
            style={{ marginTop: -18 }}
            placeholder="No Induk Barang "
            id="no_induk_barang"
            isDisabled
            value={forms.no_induk_barang}
            onChange={(e) => handleChange(e)}
            validate={error.no_induk_barang}
          />
          <CInput
            label="Barcode"
            style={{ marginTop: -18 }}
            placeholder="Barcode"
            id="tag_number"
            value={forms.tag_number}
            onChange={(e) => handleChange(e)}
            isDisabled
          />

          <CInput
            require
            label="AMS NO"
            style={{ marginTop: -18 }}
            placeholder="Nomor Barang"
            id="no_barang"
            value={forms.no_barang}
            validate={error.no_barang}
            onChange={(e) => handleChange(e)}
            disabled
          />
          <CInput
            label="REINS NO"
            style={{ marginTop: -18 }}
            placeholder="REINS NO"
            id="old_number"
            value={forms.old_number}
            validate={error.old_number}
            onChange={(e) => handleChange(e)}
          />

          <CInput
            label="Name"
            style={{ marginTop: -18 }}
            placeholder="Name"
            require
            id="name"
            validate={error.name}
            value={forms.name.toUpperCase()}
            onChange={(e) => handleChange(e)}
          />
          <CSelect
            style={{ marginTop: -18 }}
            label="Branch"
            placeholder="Branch"
            defaultValue={forms.branch_id}
            value={forms.branch_id}
            validate={error.branch_id}
            onChange={onBranch}
            options={listBranch.dataBranch}
          />
          <CSelect
            style={{ marginTop: -18 }}
            label="Departement"
            require
            placeholder="Departement"
            defaultValue={forms.vendor_id}
            validate={error.vendor_id}
            value={forms.vendor_id}
            onChange={onDepartement}
            options={departement.dataDepartement}
          />
          <CSelect
            style={{ marginTop: -18 }}
            label="Ownership"
            require
            placeholder="Ownership"
            defaultValue={forms.ownership_id}
            value={forms.ownership_id}
            validate={error.ownership_id}
            onChange={onOwnersip}
            options={listOwnership.dataOwnership}
          />
          <CSelect
            style={{ marginTop: -18 }}
            label="Code Activa"
            require
            placeholder="Code Activa"
            defaultValue={forms.code_activa_id}
            value={forms.code_activa_id}
            validate={error.code_activa_id}
            onChange={onCodeActiva}
            options={listCodeActiva.dataCodeActiva}
          />
          <CSelect
            style={{ marginTop: -18 }}
            label="Location"
            require
            validate={error.location_id}
            placeholder="Location"
            defaultValue={forms.location_id}
            value={forms.location_id}
            onChange={onLocation}
            options={listLocation.dataLocation}
          />
          <CDatePicker
            style={{ marginTop: -18 }}
            onChange={onChangeDatePerolehan}
            value={forms.tgl_perolehan}
            label="Tanggal Perolehan"
            placeholder="Tanggal Perolehan"
            require
            validate={error.tgl_perolehan}
          />
          <CSelect
            style={{ marginTop: -18 }}
            label="Jenis Barang"
            require
            placeholder="Jenis Barang"
            value={forms.asset_type_id}
            validate={error.asset_type_id}
            onChange={onAssetType}
            options={listAssetType.dataAssetType}
          />
          <CSelect
            style={{ marginTop: -18 }}
            label="Kondisi Barang"
            require
            placeholder="Kondisi Barang"
            validate={error.status_id}
            value={forms.status_id}
            onChange={onStatus}
            options={listStatus.dataStatus}
          />
        </Form>
      </Col>
      <Col span={12}>
        <Form {...formItemLayout}>
          <CInput
            label="Payment Voucher"
            style={{ marginTop: -18 }}
            placeholder="Payment Voucher"
            id="payment_voucher"
            value={forms.payment_voucher}
            onChange={(e) => handleChange(e)}
            validate={error.payment_voucher}
          />
          <CInput
            label="Merek"
            style={{ marginTop: -18 }}
            placeholder="Merek"
            id="merek_barang"
            validate={error.merek_barang}
            value={forms.merek_barang}
            onChange={(e) => handleChange(e)}
          />
          <CInput
            label="Model"
            style={{ marginTop: -18 }}
            placeholder="Model"
            id="model"
            validate={error.model}
            value={forms.model}
            onChange={(e) => handleChange(e)}
          />
          <CInput
            label="Serial Number"
            style={{ marginTop: -18 }}
            placeholder="Serial Number"
            id="serial_number"
            validate={error.serial_number}
            value={forms.serial_number}
            onChange={(e) => handleChange(e)}
          />
          <CInput
            require
            label="Harga Perolehan"
            style={{ marginTop: -18 }}
            placeholder="Harga Perolehan"
            id="harga_perolehan"
            value={forms.harga_perolehan}
            onChange={(e) => handleChange(e)}
            validate={error.harga_perolehan}
          />
          <CSelectOptionCurrency
            require
            style={{ marginTop: -18 }}
            label="Currency Perolehan"
            placeholder="Currency Perolehan"
            value={forms.currency_perolehan}
            onChange={onCurrency}
            validate={error.currency_perolehan}
          />

          <CSelect
            style={{ marginTop: -18 }}
            label="Anggaran Asset"
            require
            placeholder="Anggaran Asset"
            defaultValue={forms.anggaran_id}
            value={forms.anggaran_id}
            validate={error.anggaran_id}
            onChange={onAnggaran}
            options={listAnggaran.dataAnggaran}
          />

          <CInput
            require
            label="Massa Manfaat (Year)"
            style={{ marginTop: -18 }}
            placeholder="Massa Manfaat (Year)"
            id="masa_manfaat"
            value={forms.masa_manfaat}
            onChange={(e) => handleChange(e)}
            validate={error.masa_manfaat}
          />
          <CSelectOptionActiva
            require
            style={{ marginTop: -18 }}
            label="Status Activa"
            placeholder="Status Activa"
            value={forms.status_activa}
            onChange={onActiva}
            validate={error.status_activa}
          />
          <CSelectOptionFlag
            require
            style={{ marginTop: -18 }}
            label="Flag"
            placeholder="Flag"
            value={forms.flag}
            onChange={onFlag}
            validate={error.flag}
          />
          <CTextArea
            label="Remark"
            placeholder="Remark"
            id="remark"
            value={forms.remark}
            onChange={onRemark}
            style={{ marginTop: -18 }}
          />
        </Form>
      </Col>
    </Row>
  );
};

export default CEditAssetInput;
